@use '../base/variables' as v;
@use '../base/mixins' as m;

.video {
    position: relative;
    max-height: 60rem;
    min-height: 40rem;
    overflow: hidden;

    .overlay {
        position: absolute;
        background: rgb(165,0,255);
        background: linear-gradient(270deg, rgba(165,0,255,0.4) 0%, rgba(255,0,110,0.4) 100%);
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }

    video {
        width: auto;
        display: block;
        @include m.desktop {
            width: 100%;
        }
    }
}

.contenido-video {
    h2 {
        color: v.$blanco;
        font-size: 7rem;
        margin: 0px;
    }
    p {
        background-color: v.$rosa;
        display: inline-block;
        padding: 1rem 2rem;
        color: v.$blanco;
        font-weight: 700;
        font-size: 2.4rem;
        margin: 0px;
    }
}