@use '../base/variables' as v;
@use '../base/mixins' as m;


.header {
    background-color: v.$verde;
    @include m.tablet {
        &.fijo {
            left: 0;
            top: 0;
            position: fixed;
            width: 100%;
            box-shadow: .1rem .2rem .3rem #333;
        }
    }
    h1 {
        color: v.$blanco;
        text-align: center;
        margin: 0px;
    }
    .contenido-header {
        padding: 2rem;
        @include m.tablet {
            display: flex;
            justify-content: space-between;
            align-items: center;
        }
    }
}

.navegacion-principal {
    @include m.tablet {
        display: flex;
        gap: 3rem;
    }
    a {
        color: v.$blanco;
        font-size: 2.4rem;
        display: block;
        text-align: center;

        &:hover {
            color: v.$amarillo;
        }
    }
}

